<template>
  <v-app id="inspire">
    <v-app-bar
        app
        color="white"
        flat
    >
      <v-container class="py-0 fill-height">
        <v-avatar
            class="mr-10"
            color="grey darken-1"
            size="32"
        ></v-avatar>
        <v-spacer></v-spacer>
        <v-btn

            rounded
            text
            v-for="link in links"
            :key="link.message"
            @click="goTo(link.router)"
        >

          {{ link.message }}
        </v-btn>


      </v-container>
    </v-app-bar>
    <v-main class="grey lighten-3">
      <v-container>
        <v-row>
          <v-col cols="2">
            <v-sheet rounded="lg">
              <v-list color="transparent">
                <v-list-item
                    v-for="n in 5"
                    :key="n"
                    link
                >
                  <v-list-item-content>
                    <v-list-item-title>
                      List Item {{ n }}
                    </v-list-item-title>
                  </v-list-item-content>
                </v-list-item>

                <v-divider class="my-2"></v-divider>

                <v-list-item
                    link
                    color="grey lighten-4"
                >
                  <v-list-item-content>
                    <v-list-item-title>
                      Refresh
                    </v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
              </v-list>
            </v-sheet>
          </v-col>

          <v-col>
            <v-card>
              <slot></slot>
            </v-card>
          </v-col>
        </v-row>
      </v-container>

    </v-main>

  </v-app>
</template>

<script>
export default {
  data: () => ({
    links: [
      {
        id: 1,
        message: 'Login',
        router: '/login',
      }, {
        id: 2,
        message: 'Register',
        router: '/register',
      },

    ],


  }),
  methods: {
    goTo(nameRouter) {
      this.$router.push(nameRouter)
    },

  }
}
</script>